import { useState, useEffect } from "react";

import Uploader from "./Uploader";
import Merger from "./Merger";
import PhotosDisplay from "./PhotosDisplay";
import { ButtonGroup, TextField } from "@material-ui/core";
import LinearProgress from "@material-ui/core/LinearProgress";
import Sorter from "./Sorter";

const HTML5Backend = require("react-dnd-html5-backend").HTML5Backend;
const DndProvider = require("react-dnd").DndProvider;

export default function App() {
  const [photos, setPhotos] = useState([]);
  const [progress, setProgress] = useState(0);
  const [width, setWidth] = useState(1024);
  const [height, setHeight] = useState(1024);

  useEffect(() => {
    console.log(photos);
  }, [photos]);

  let progressBar = "";
  if (progress > 0 && progress < 100)
    progressBar = (
      <LinearProgress variant="determinate" value={progress * 100} />
    );

  return (
    <>
      {progressBar}
      <br />
      <Sorter setPhotos={setPhotos}></Sorter>
      <br />
      <br />
      <form noValidate autoComplete="off">
        <TextField
          value={width}
          onChange={(e) => setWidth(e.target.value)}
          id="standard-basic"
          label="Max width"
        />
        &nbsp;
        <TextField
          value={height}
          onChange={(e) => setHeight(e.target.value)}
          id="standard-basic"
          label="Max height"
        />
      </form>

      <DndProvider backend={HTML5Backend}>
        <PhotosDisplay
          width={width}
          height={height}
          setPhotos={setPhotos}
          photos={photos}
        ></PhotosDisplay>
      </DndProvider>

      <ButtonGroup
        variant="contained"
        color="primary"
        aria-label="contained primary button group"
      >
        <Uploader setPhotos={setPhotos}></Uploader>
        <Merger photos={photos} setProgress={setProgress}></Merger>
      </ButtonGroup>
    </>
  );
}
